import { createRouter, createWebHistory } from 'vue-router';

// Главная страница и другие общие компоненты
import HomeView from '../views/HomeView.vue';
import AboutView from '../views/AboutView.vue';
import ContactsView from '../views/ContactsView.vue';

// Компоненты для медицинских товаров
import MedicalGoodsList from '../views/MedicalGoodsList.vue';
import MedicalGoodsAdd from '../views/MedicalGoodsAdd.vue';
import MedicalGoodsEdit from '../views/MedicalGoodsEdit.vue';
import MedicalGoodsView from '../views/MedicalGoodsView.vue';

// Компоненты для лекарств
import MedicinesList from '../views/MedicinesList.vue';
import MedicinesAdd from '../views/MedicinesAdd.vue';
import MedicinesEdit from '../views/MedicinesEdit.vue';
import MedicinesView from '../views/MedicinesView.vue';

// Компоненты для авторизации
import UserRegister from '../components/UserRegister.vue';
import UserLogin from '../components/UserLogin.vue';
import AdminDashboard from '../components/AdminDashboard.vue';
import UnauthorizedView from '../views/UnauthorizedView.vue';

const routes = [
  { path: '/', name: 'Home', component: HomeView },
  { path: '/about', name: 'About', component: AboutView },
  { path: '/contacts', name: 'Contacts', component: ContactsView },
  {
    path: '/medical-goods',
    name: 'MedicalGoodsIndex',
    component: MedicalGoodsList,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/medical-goods/add',
    name: 'MedicalGoodCreate',
    component: MedicalGoodsAdd,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/medical-goods/edit/:id',
    name: 'MedicalGoodEdit',
    component: MedicalGoodsEdit,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/medical-goods/view/:id',
    name: 'MedicalGoodsView',
    component: MedicalGoodsView,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/medicines',
    name: 'MedicinesIndex',
    component: MedicinesList,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/medicines/add',
    name: 'MedicineCreate',
    component: MedicinesAdd,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/medicines/edit/:id',
    name: 'MedicineEdit',
    component: MedicinesEdit,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/medicines/view/:id',
    name: 'MedicineView',
    component: MedicinesView,
    meta: { requiresAuth: true, role: 'admin' }
  },
  { path: '/login', name: 'Login', component: UserLogin },
  { path: '/register', name: 'Register', component: UserRegister },
  { path: '/dashboard', name: 'Dashboard', component: AdminDashboard },
  {
    path: '/unauthorized',
    name: 'Unauthorized',
    component: UnauthorizedView // Используйте новое имя компонента
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

// Навигационный хук для защиты маршрутов
router.beforeEach(async (to, from, next) => {
  if (to.meta.requiresAuth) {
    try {
      const response = await fetch('/protected');
      if (!response.ok) {
        return next('/login'); // Перенаправляем на страницу входа
      }

      const data = await response.json();
      const userRole = data.role;

      if (to.meta.role && userRole !== to.meta.role) {
        return next('/unauthorized'); // Перенаправляем на страницу "Нет доступа"
      }

      next(); // Разрешаем доступ
    } catch (error) {
      console.error('Ошибка при проверке авторизации:', error);
      next('/login'); // Перенаправляем на страницу входа
    }
  } else {
    next(); // Разрешаем доступ к другим маршрутам
  }
});

export default router;